import React, { useRef, useEffect, useCallback, useState} from 'react';
import Credits from "components/credits/Credits.js";
import SongTitle from './SongTitle.js';
import SporePlaylist from 'components/playlist/SporePlaylist.js';
import {connect} from 'react-redux';
import {isMobile} from 'react-device-detect';
import './caurosel.scss';

const Caurosel = ({playerChosen, setPlayerChosen, showStemPlayer, setShowStemPlayer}) => { 
    let ref = useRef();
    let [selectedPlayer, setSelectedPlayer] = useState(undefined);
    let [mouse, setMouse] = useState();
    let [over, setOver] = useState();

    const onMouseMove = useCallback((e) => {
        let rect = ref.current.getBoundingClientRect();
        let x = e.clientX - rect.left;
        let y = e.clientY - rect.top;
        setMouse({x,y});
    }, [setMouse]);

    const onMouseLeave = useCallback(() => {
        setMouse(undefined);
    }, [setMouse]);

    let brect = ref.current && ref.current.getBoundingClientRect();

    let className = "caurosel";
    if (playerChosen) {
        className += " player-chosen";
    }
  
    let tokens = new Array(20).fill(0);
    for (let i=1; i < 20; i++) {
        tokens[i-1] = i;
    }
    return (
        <div onMouseLeave={onMouseLeave} ref={ref} onMouseMove={onMouseMove} className={className}>
          {mouse && <div className="mouse-radial" style={{left: mouse.x-500, top: mouse.y-500}}></div>}
          <SporeOption
                playerChosen={playerChosen}
                brect={brect}
                setShowStemPlayer={setShowStemPlayer}
                showStemPlayer={showStemPlayer}
                mouse={mouse}
                isOver={over===playerChosen}
                setOver={setOver}
                setPlayerChosen={setPlayerChosen} rarity="" tokenId={playerChosen}
                thumbnail={isMobile ? "/sp-" + playerChosen + ".png" : '/sp-' + playerChosen + '.png'}
              />
       </div>);
};

const SporeOption = ({
    showStemPlayer,
    setShowStemPlayer,
    brect,
    tokenId, playerChosen, isOver, mouse, setOver, rarity, thumbnail, setPlayerChosen
}) => {
    let ref= useRef();
    let [currentSong, setCurrentSong] = useState("Twisting Faith");

    let rect = ref.current && ref.current.getBoundingClientRect();
    let _x = rect && mouse && mouse.x - rect.left + brect.left;
    let _y= rect && mouse && mouse.y - rect.top + brect.top;
    let className = "spore-player-option";
    if (playerChosen === tokenId) {
        className += " chosen";
    } else if (playerChosen) {
        className += " unchosen";
    }

        const onClick = useCallback(() => {
              if (showStemPlayer) {
                  setShowStemPlayer(undefined);
                  setPlayerChosen(undefined);
                  return;
              }
              if (playerChosen === tokenId) {
                  setShowStemPlayer(undefined);
                  setPlayerChosen(undefined);
              } else {
                  setPlayerChosen(tokenId);
                  setShowStemPlayer(true);
              }
        }, [setPlayerChosen, showStemPlayer, playerChosen, tokenId]);

    return (
        <div
          ref={ref}
          onMouseOver={() => setOver(tokenId)}
          className={className}>
          {isOver && mouse && <div className="mouse-radial" style={{left: _x-500, top: _y-500}}></div>}
          <div
            onClick={onClick}>
            <img src={thumbnail}/>
            <div
              className="sp-fields">
              <div
                className="sp-name">
                SP-{tokenId}
              </div>
              <div className="sp-rarity">
                {rarity}
              </div>
            </div>
          </div>
          {showStemPlayer && <SongTitle/>}
          {!isMobile && showStemPlayer && playerChosen === tokenId && <SporePlaylist currentSong={currentSong} setCurrentSong={setCurrentSong}/>}
          {showStemPlayer && playerChosen === tokenId && <Credits/>}
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Caurosel);
